}

.mainFooter {
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(7, 7, 9, 0.8)), to(rgba(7, 7, 9, 0.8))), url("../media/app/footer-bg.jpg");
    background: -webkit-linear-gradient(rgba(7, 7, 9, 0.8), rgba(7, 7, 9, 0.8)), url("../../images/slider/wind-slider-4.jpg");
    background: -o-linear-gradient(rgba(7, 7, 9, 0.8), rgba(7, 7, 9, 0.8)), url("../../images/slider/wind-slider-4.jpg");
    background: linear-gradient(rgba(7, 7, 9, 0.8), rgba(7, 7, 9, 0.8)), url("../../images/slider/wind-slider-4.jpg");
    background-color: #070709;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    color: #FFFFFF; }
.mainFooter a:hover {
    text-decoration: underline; }
.mainFooter .logo {
    margin: 3.12em auto;
    max-width: 180px;
    height: auto; }
.mainFooter .termsAndSocial {
    padding: 2em 0;
    margin: 1.87em 0;
    line-height: 1.5;
    border-top: 1px solid #979797;
    border-bottom: 1px solid #979797; }
.mainFooter .termsAndSocial {
    *zoom: 1; }
.mainFooter .termsAndSocial:before, .mainFooter .termsAndSocial:after {
    content: "";
    display: table; }
.mainFooter .termsAndSocial:after {
    clear: both; }
.mainFooter .copyright {
    padding-bottom: 2em;
    text-align: center; }


.list.-footer {
    position: relative;
    display: inline-block;
    vertical-align: top;
    width: 100%; }
@media only screen and (min-width: 600px) {
    .list.-footer {
        width: 49%; } }
@media only screen and (max-width: 1024.98px) {
    .list.-footer:nth-last-child(-n+2) {
        margin-top: 1.5em; } }
@media only screen and (min-width: 1025px) {
    .list.-footer {
        float: left;
        width: 25%; }
    .list.-footer:last-child {
        padding-left: 12%; }
    .list.-footer:last-child:before {
        content: "";
        border-left: 1px solid #979797;
        position: absolute;
        left: 20%;
        top: -10px;
        bottom: -10px; } }
.list.-footer .list_item {
    line-height: 1.5; }

@media only screen and (min-width: 1025px) {
    .list.-terms {
        float: left; } }

.list.-terms .list_item:not(:first-child) {
    padding-left: 0.5em; }
.list.-terms .list_item:not(:first-child):before {
    content: "/";
    margin-right: 0.5em; }

@media only screen and (min-width: 1025px) {
    .mainFooter .list.-social {
        float: right; } }

@media only screen and (max-width: 1024.98px) {
    .mainFooter .list.-social {
        border-top: 1px solid;
        padding-top: 2em;
        margin-top: 2em;
        text-align: center; } }

.list.-social .list_item:not(:last-child) {
    margin-right: 0.5em; }

