  border: none;
  border-radius: 0;
  padding: 10px 25px;
  border: 1px solid transparent;
  -moz-transition: all, 0.3s;
  -o-transition: all, 0.3s;
  -webkit-transition: all, 0.3s;
  transition: all, 0.3s;
}
.btn-primary:hover {
  background: #fff;
  color:   rgba(7, 7, 9, 0.8);
  border: 1px solid   rgba(7, 7, 9, 0.8);
}

.btn-secondary {
  background: #fff;
  color:   rgba(7, 7, 9, 0.8);
  border-radius: 0;
}
.btn-secondary:hover {
  background:   rgba(7, 7, 9, 0.8);
  color: #fff;
}

.title-style-1 {
  text-align: center;
  text-transform: uppercase;
  font-size: 26px;
  margin-bottom: 30px;
}

.title-style-2 {
  text-align: left;
  text-transform: uppercase;
  font-size: 26px;
  margin-bottom: 25px;
}
.title-style-2 .title-under {
  display: block;
  margin: 5px 0;
}
.title-style-2 .title-under:after {
